.media {
    margin: 1rem;
    list-style-type: none;
    
}


.media i {
    font-size: 2rem;
    color: #000;
    margin: 10px;
    opacity: 0.9;
}


* {
	margin: 0;
	padding: 0;
}

.App {
	text-align: center;
}

.myname {
	margin-top: 4rem;
}

.space {
	margin-right: 10px;
}

.main {
  width: 350px;
  height: 350px;
	margin: 10px auto;
	border: 1px dashed rgb(124, 187, 255);
	box-shadow: 0px 2px 8px -2px rgba(133, 133, 133, 1);
}

nav {
    height: 60px;
    padding: 10px;
    border: 1px solid rgb(124, 187, 255);
    background-color:  rgb(124, 187, 255);
}

.main-logo {
    display: flex;
    justify-content: center;
    align-items: center;
}

.main-logo img {
    width: 60px;
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
}

@-webkit-keyframes App-logo-spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
}

.logo-text  {
    text-align: center;
    font-family: 'Bungee';
}






@media only screen and (min-width: 600px) {
   
    .logo-text  {
        font-size: 1.8rem;
    }
  }
